 .button-4:hover {
   text-decoration: none;
   transition-duration: 0.1s;
   opacity: 0.8;
 }
 .button-4:disabled {
    cursor: not-allowed;
  }
  select:disabled{
    cursor: not-allowed;
 }
  .button-4:active {
    box-shadow: rgba(50, 66, 86, 0.2) 0 1px 0 inset;
    transition: none 0s;
  }
  .button-4:focus {
    outline: 1px transparent;
  }
  .button-4:before {
    display: none;
  }
  .button-4:-webkit-details-marker {
    display: none;
  }
